import API from '@/API'

export default {
	data(){
		return{
			items:[]
		}
	},
	created(){
		this.getReport( this.$route.params.id )
	},
	methods:{
		async getReport(id){
			let res = await API.getReport(id)
			console.log(res.data)
			console.log(res.data.data)
			this.items = res.data.data
		}
	}
}



